import styles from "./calender.module.css"

const EvenItem = ({ info }) => {
  const { event } = info

  const type = event._def.extendedProps.type
  //     .
  // , , 
  // console.log(event)
  return (
    <div
      className={
        type === ""
          ? styles.eventSpanAttend
          : type === ""
          ? styles.eventSpanAbscent
          : styles.eventSpanStatus
      }
    >
      <div
        className={
          type === ""
            ? event.title === ""
              ? styles.abscentColor
              : (event.title === "") | (event.title === "")
              ? styles.tardyColor
              : styles.attendColor
            : styles.none
        }
      >
        {event.title}
      </div>
    </div>
  )
}

export default EvenItem
