import { useState } from "react";
import { useEffect } from "react";
import TeamMemberRow from "./teamMemberRow.";
import styles from "./teamMember.module.css";
import axios from "axios";
import { API_URL } from "../../../common/api";
import axios1 from "../../../common/api/axios";

const TeamMember = (props) => {
  localStorage.getItem("teamCode");

  const [team, setTeam] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const teamCode = localStorage.getItem("teamCode");

    axios1
      .get(API_URL + `attendance/team/${teamCode}`, {
        headers: {
          accessToken: window.localStorage.getItem("token"),
        },
      })
      .then((res) => {
        const data = res.data;
        const index = data.findIndex((stduent) => stduent.id === props.userId);
        data.splice(index, 1);
        setTeam(data.slice(0, 5));
      });

    setIsLoading(false);
  }, []);

  console.log(props.userId);

  return (
    !isLoading && (
      <div>
        <div className={styles.teamMemberTitle}> </div>
        <div className={styles.teamMemberOverlay}>
          <table className={styles.table}>
            {team.map((student) => {
              return (
                <TeamMemberRow
                  key={student.id}
                  student={student}
                  setIds={props.setIds}
                  mmsHandler={props.mmsHandler}
                />
              );
            })}
          </table>
        </div>
      </div>
    )
  );
};

export default TeamMember;
